export default {
	data() {
		return {
			user_token:'',
			form:{
				headerimg:'',
				realname:'',
				vipstatus:false,
				wdxf:'',
				yxkc:'',
				yxsc:'',
			},
			mylist:[
				{ id: 1, name: "个人信息", src:"/static/use/yonghuming.png"},
				{ id: 2, name: "我的会员", src:"/static/use/xingxing.png"},
				{ id: 3, name: "我的课程", src:"/static/use/wenjianjia.png"},
				{ id: 4, name: "我的成就", src:"/static/use/biaoqian.png"},
			],
			mylist2: [
				{ id: 1, name: "关于华职", src: "/static/use/gongneng.png" },
				{ id: 2, name: "意见反馈", src: "/static/use/xinxi.png" },
			]
		}
	},
	onShow() {
		this.getdata();
	},
	created(){
		
		
	},
	onLoad() {
		var that = this;
		uni.getStorage({
		    key: 'user_token',
		    success: (res)=> {
					console.log(res)
		      that.user_token = res.data;
		    }
		});
		this.getdata();
		// this.getBanner();
		// this.getClass();
	},
	methods:{
		toDetail(e){
			var that = this;
			console.log(e)
			if (e == '个人信息') {
				wx.navigateTo({
					url: '/pages/user/setuser',
				})
			} else if (e == '我的会员') {
				wx.navigateTo({
					url: '/pages/user/member',
				})
			} else if (e == '我的课程') {
				wx.navigateTo({
					url: '/pages/user/myclass',
				})
			} else if (e == '我的成就') {
				wx.navigateTo({
					url: '/pages/user/success',
				})
			} else if (e == '关于华职') {
				wx.navigateTo({
					url: '/pages/user/aboutus',
				})
			} else if (e == '意见反馈') {
				wx.navigateTo({
					url: '/pages/user/feedback',
				})
			}
		},
		toIndex(){
			wx.navigateTo({
				url: '/pages/index/index',
			})
		},
		getdata(){
			uni.request({
				url: this.$url+'api/user/personalcenter'+this.user_token,
				method: 'post',
				data: {},
				success: res => {
					console.log(res,'信息');
					if(res.data.code == 10000){
						this.form = res.data.data;//this.lunboList = res.data.data
					}else{
						this.$msg(res.data.msg)
					}
				},
			})
		},
	},
}
